:root {
    --primary-color-900: #0d1c2d;
    --secondary-color-400: #0067b1;
    --neutral-color-800: #122741;
    --neutral-gray-500: #999999;

    --ff-primary: "Montserrat", sans-serif;

    --bg-neutral-200: #f9f9f9;

    --fw-light-300: 300;
    --fw-regular-400: 400;
    --fw-semibold-600: 600;
    --fw-bold-700: 700;

    --sp-32: 32px;
}

@import "./variables.scss";
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;600;700&display=swap");

*,
*::before,
*::after {
    box-sizing: border-box;
}

body {
    font-family: var(--ff-primary);
    font-weight: 300;
    font-size: 62.5%;
    line-height: 1.6;
    height: 100%;
}

body,
h1,
h2,
h3,
p {
    margin: 0;
}

p {
    font-size: 18px;
    font-weight: var(--fw-regular-400);
    line-height: 22px;
}

span {
    margin-left: 0 !important;
}

a {
    text-decoration: none;
    color: #fff;
}

h1 {
    font-size: 150px;
    font-weight: 300;
    color: var(--primary-color-900);
}

section {
    height: 100%;
    width: 100%;
    padding: 5em 2em;
}

.container {
    margin-inline: auto;
    width: calc(min(90%, 94rem));
}

/******Navbar-Menu*******/

.logo {
    margin: auto 0;
    padding-right: 43px;
}

.navbar {
    background: rgba(18, 39, 65, 0.3);
    backdrop-filter: blur(20px);
    height: 68px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;
}

.navbar .container {
    width: 83%;
    display: flex;
    justify-content: space-between;
}

.menu-center {
    position: relative;
    width: 68px;
    height: 68px;
    cursor: pointer;
    backdrop-filter: blur(4px);
}

.menu-bars {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.nav-menu {
    background: rgba(18, 39, 65, 0.3);
    backdrop-filter: blur(20px);
    width: 157px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -157px;
    transition: 500ms;
    z-index: 1000;
}

.nav-menu.active {
    left: 0;
}

.nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    list-style: none;
    height: 10vh;
}

.nav-menu-items a {
    text-decoration: none;
    color: #fff;
    font-size: 21px;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 16px;
    transition: 250ms;
}

.nav-menu-items a:hover {
    background-color: #11324b;
    transition: 150ms;
}
.nav-menu-items {
    width: 100%;
    padding: 0;
}

.navbar-toggle {
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}

span {
    margin-left: 16px;
}

.menu-text {
    font-weight: 300;
}

/*****************/

.button-text {
    font-size: 21px;
    line-height: 26px;
}

.vertical-scroll {
    position: absolute;
    right: 10.7%;
    top: 23%;
}

.hero {
    height: 100vh;
    .left-column {
        max-width: 66rem;
    }
    .right-column {
        padding-top: 15%;
    }

    h3 {
        font-size: 6.2em;
        font-weight: 300;
        line-height: 76px;
        max-width: 66rem;
        text-transform: uppercase;
        color: white;
    }
    p {
        padding: 14px 58px;
        font-size: 21px;
    }
    .primary-cta {
        display: flex;
        align-items: center;
        margin-top: 60px;
        span {
            display: inline-block;
            text-align: center;
            background-color: #677d8e;
            transition: 0.5s;
        }
        img {
            background-color: var(--primary-color-900);
            padding: 18px 20px;
            transition: 0.5s;
        }
    }
    .secondary-cta {
        display: inline-block;
        span {
            display: inline-block;
            margin-right: 1em;
        }
    }
}

/************Hero Static****************/

.hero-static {
    height: 100vh;
    .left-column {
        max-width: 66rem;
    }
    .right-column {
        padding-top: 8%;
    }

    h3 {
        font-size: 6.2em;
        font-weight: 300;
        line-height: 1;
        max-width: 66rem;
        text-transform: uppercase;
        color: white;
    }
    p {
        padding: 14px 58px;
        font-size: 21px;
    }
    .primary-cta {
        display: flex;
        align-items: center;
        margin-top: 60px;
        span {
            display: inline-block;
            text-align: center;
            background: rgba(255, 255, 255, 0.2);
            backdrop-filter: blur(30px);
            transition: 0.5s;
        }
        img {
            background-color: var(--primary-color-900);
            padding: 18px 20px;
            transition: 0.5s;
        }
    }
    .secondary-cta {
        display: inline-block;
        span {
            display: inline-block;
            margin-right: 1em;
        }
    }
}

.hero-static .container {
    display: flex;
    place-content: space-between;
    padding-top: 55vh;
    text-align: left;
}

/*********Functional**************/

.primary-cta span:hover {
    background-color: var(--primary-color-900);
}
.primary-cta img:hover {
    background-color: var(--secondary-color-400);
}

.hero .container {
    display: flex;
    place-content: space-between;
    padding-top: 55vh;
    text-align: left;
}
#ReactBackgroundSlider {
    z-index: -1;
}

.bg-neutral {
    background-color: #f9f9f9;
}

/*****HomeSection*****/

.home-section {
    height: 100%;
    width: 100%;
}

.split {
    display: flex;
    flex-direction: column;
}

@media (min-width: 50em) {
    .split {
        flex-direction: row;
    }
}

.normal-cta {
    color: var(--primary-color-900);
    font-weight: 400;
    span {
        font-size: 1.5rem;
        padding-right: 0.5em;
    }
}

.normal-cta div {
    display: inline-block;
}

.image-section {
    width: 50vw;
    margin-bottom: -5px;
    z-index: 2;
    img {
        width: 100%;
    }
}

.text-section {
    position: relative;
    width: 50vw;
    overflow-x: hidden;
    h1 {
        line-height: 1;
        margin-bottom: 40px;
        margin-left: -15px;
    }
    p {
        font-size: 1.5rem;
        max-width: 25rem;
        line-height: 1.3;
    }
    .text-section-container {
        position: absolute;
        left: 36%;
        top: 36%;
    }
}

.description {
    margin-bottom: 40px;
}

.number-line {
    display: flex;
    flex-direction: row;
    margin-bottom: 5em;
    p {
        font-size: 18px;
        margin-right: 12px;
    }
    span {
        width: 31vw;
        border-bottom: 1px solid var(--primary-color-900);
        margin-bottom: 5px;
        overflow-x: hidden;
    }
}

/***********HomeSection Form*************/

.home-section-form {
    display: flex;
    height: 100%;
    width: 100%;
}

.form-section {
    width: 50%;
    min-height: 100%;
}

.form-section .line {
    width: 100%;
}

.form-section .number-line {
    margin-bottom: 2em;
}
.form-section-container {
    width: 80%;
    margin: 70px 0 60px auto;
}

/***********Run**************/

.bg-run {
    background-image: url("./assets/images/image-9.jpg");
    background-position: center;
    background-size: cover;
}

/***********SubHero**********/

.bg-sub-hero {
    background-image: url("./assets/images/image-8.jpg");
    background-position: center;
    background-size: cover;
}

.sub-hero-viewport {
    min-height: 100vh;
}

.sub-hero {
    width: 100%;
    height: 100%;
    .number-line {
        span {
            width: 100%;
        }
    }
    .sub-hero-container {
        padding-top: 25vh;
        padding-left: 350px;
        .split {
            .big-title {
                margin-right: 72px;
                margin-top: -5em;
                width: 557px;
                h1 {
                    margin-left: -0.1em;
                    color: var(--primary-color-900);
                }
                h4 {
                    font-size: 62px;
                    color: var(--neutral-color-800);
                    font-weight: var(--fw-semibold-600);
                    margin: 0;
                    padding-top: 50px;
                    line-height: 76px;
                }
                h5 {
                    font-size: 62px;
                    color: var(--neutral-color-800);
                    font-weight: var(--fw-semibold-600);
                    margin: 0;
                    line-height: 76px;
                }
                p {
                    margin-top: -3em;
                    color: var(--primary-color-900);
                    line-height: 22px;
                    font-size: 18px;
                    max-width: 300px;
                }
            }
            .subtitle-text {
                max-width: 810px;
                h3 {
                    margin: 0;
                }
                p {
                    font-size: 18px;
                    line-height: 22px;
                    font-weight: var(--fw-regular-400);
                    color: #737373;
                    width: 760px;
                }
                .firstLine {
                    margin-bottom: 1em;
                }
            }
        }
    }
}

p .blue-text {
    color: #122741;
}

.sub-hero .sub-hero-container .split .subtitle-text p {
    // font-size: 1rem;
}
/*********Run Content**************/

.run-content {
    background-color: var(--bg-neutral-200);
    height: 100%;
}

summary {
    color: var(--primary-color-900);
    font-size: 80px;
    cursor: pointer;
    outline: none;
}

details {
    padding: 90px 0;
}

details[open] summary ~ * {
    animation: sweep 0.5s ease-in-out;
}

.drop-content {
    padding-top: 45px;
}

details p {
    font-size: 1rem;
    max-width: 810px;
    margin-bottom: 1em;
    color: var(--neutral-gray-500);
}

details p span {
    margin-bottom: 1em;
}

summary::marker {
    background: url("./assets/images/drop-down-drk.svg") center no-repeat;
    // color: transparent;
    content: "";
}

@keyframes sweep {
    from {
        opacity: 0;
        margin-top: -10px;
    }
    to {
        opacity: 1;
        margin-top: 0;
    }
}

