/**********See**********/

.bg-see {
    background-image: url("./assets/images/image-15.jpg");
    background-position: center;
    background-size: cover;
}

.centered {
    margin: 0 auto;
    max-width: 85vw;
}

.subtitle-text h3 {
    font-size: 2rem;
