.bg-ask {
    background-image: url("./assets/images/image-22.jpg");
    background-position: center;
    background-size: cover;
}

.sub-hero-ask {
    background-image: url("./assets/images/image-8.jpg");
}

.map-container-ask {
    min-height: 80vh;
    height: 80vh;
    display: flex;
    justify-content: center;
    margin: 12em 0 7em 0;
}

.map-container img {
