/*******Form************/

.form-container {
    max-width: 605px;
    // width: 100%;
    // font-size: 12px;
    color: #999;
}

.form-container ::placeholder {
    color: #999;
}

.gender-details {
    margin-bottom: var(--sp-32);
}

.gender-details .category label {
    margin-right: 26px;
}

.contact-preference {
    margin-bottom: var(--sp-32);
}

.contact-preference .category label {
    margin-right: 100px;
}

.content form .user-details {
    display: flex;
    flex-wrap: wrap;
    width: 80%;
}

form .user-details .input-box {
    margin-bottom: var(--sp-32);
    width: 191px;
    margin-right: 16px;
}

form .input-box span.details {
    display: block;
    font-weight: 500;
    margin-bottom: 5px;
}

.user-details .input-box input {
    height: 35px;
    width: 100%;
    outline: 0;
    border-width: 0 0 2px;
    border-bottom-width: 1px;
    border-bottom-color: var(--neutral-gray-500);
    transition: all 0.3s ease;
    background: none;
}

.user-details .input-box input:focus,
.user-details .input-box input:valid {
    border-color: #0067b1;
}

form .contact-preference .contact-title,
form .checkbox-group .interests-title {
    font-weight: 300;
    margin-bottom: 1em;
}

form .category {
    display: flex;
}

.wider {
    margin: var(--sp-32) 0;
}

.check-grid {
    margin-bottom: 0;
}

form .category label {
    display: flex;
    align-items: center;
    cursor: pointer;
}
form .category label .dot {
    height: 18px;
    width: 18px;
    border-radius: 50%;
    margin-right: 10px;
    background: #d9d9d9;
    border: 5px solid transparent;
    transition: all 0.3s ease;
}

#dot-1:checked ~ .category label .one,
#dot-2:checked ~ .category label .two,
#dot-3:checked ~ .category label .three,
#dot-4:checked ~ .category label .four,
#dot-5:checked ~ .category label .five {
    background: #fff;
    border-color: #0d1c2c;
}

.checkbox-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    width: 90% !important;
}

.checkbox-wrapper {
    display: flex;
    align-items: center;
    padding: 0 32px 32px 0;
}

.interests-title {
    display: block;
    margin-bottom: 32px;
}

.checkbox-wrapper label {
    cursor: pointer;
    padding-left: 9px;
}

input[type="checkbox"] {
    appearance: none;
    -webkit-appearance: none;
    height: 20px;
    width: 20px;
    outline: 1px solid #999999;
    cursor: pointer !important;
}

input[type="checkbox"]:after {
    content: "\2713";
    font-size: 15px;
    color: #999;
    display: none;
    -webkit-transform: rotate(15deg);
    -ms-transform: rotate(15deg);
    transform: rotate(15deg);
}

input[type="checkbox"]:checked:after {
    display: block;
    text-align: center;
}

.checkbox-group .category {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    width: 90% !important;
}

.checkbox-group label {
    padding: 0 32px 32px 0;
}

form input[type="radio"] {
    display: none;
}

form .button {
    height: 45px;
    margin: 25px 0 0 auto;
    width: 120px;
}
form .button input {
    height: 100%;
    width: 100%;
    border-radius: 2px;
    border: none;
    color: #fff;
    font-weight: 500;
    letter-spacing: 1px;
    cursor: pointer;
    transition: all 0.3s ease;
    background-color: #122741;
}
form .button input:hover {
    background-color: #0067b1;
}
@media (max-width: 584px) {
    .container {
        max-width: 100%;
    }
    form .user-details .input-box {
        margin-bottom: 15px;
        width: 100%;
    }
    form .category {
        width: 100%;
    }
    .content form .user-details {
        max-height: 300px;
        overflow-y: scroll;
    }
    .user-details::-webkit-scrollbar {
        width: 5px;
    }
}
@media (max-width: 459px) {
    .container .content .category {
        flex-direction: column;
    }
}

textarea {
    outline: none;
    resize: none;
    min-width: 605px;
    padding: 1em;
    border-radius: 5px;
    background: none;
}

textarea::placeholder {
    color: #fff;
}

input,
textarea,
select {
    font-family: inherit;
}

/*****Footer*******/

.footer {
